<template>
  <div class="boxing">
    <h4>客户详细信息</h4>
    <el-form ref="form" :model="form" label-width="150px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="客户编号">
            <el-input v-model="form.mid"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="客户姓名">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="手机号">
            <el-input v-model="form.mobile"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="身份证号">
            <el-input v-model="form.identify"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="性别">
            <!-- <el-input v-model="form.sex"></el-input> -->
            <el-select v-model="form.sex" placeholder="请选择">
              <el-option
                v-for="item in sexOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-form-item>
            <el-button type="primary" @click="save">保存</el-button>
            <el-button @click="cancel">取消</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
import { constants } from "fs";
export default {
  data() {
    return {
      form: {
        mid: "",
        name: "",
        mobile: "",
        identify: "",
        sex: 1,
        headImage:""
      },
      sexOptions: [{
        value: 1,
        label: '男'
      },{
        value: 0,
        label: '女'
      }],
    };
  },
  created() {
    var that = this;
    if (this.$route.query.id) {
      const id = this.$route.query.id;
      that.$get("/members/" + id)
        .then(function(res) {
          that.form = res.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    } 
  },
  methods: {
    save() {
      var that = this;
      var id = that.form.id;
      var mid = that.form.mid;
      var name = that.form.name;
      var mobile = that.form.mobile;
      var identify = that.form.identify;
      var sex = that.form.sex;
      var headImage = that.form.headImage;
      if (this.$route.query.id) {
        console.log("1")
        // 修改
        this.$axios({
          method: "put",
          url: "/members",
          // headers: { Authorization: sessionStorage.getItem("auth") },
          data: {
              id:id,
              mid: mid,
              name: name,
              mobile: mobile,
              identify: identify,
              sex: sex,
              headImage: headImage
          }
        })
          .then(function(res) {
            console.log(res);
            if (res.status == 200) {
              that.$message({
                message: "修改成功",
                type: "success"
              });
              that.$router.push("/member/list");
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        // 添加
        this.$post(
            "/members",
            {
              mid: mid,
              name: name,
              mobile: mobile,
              identify: identify,
              sex: sex,
              headImage: headImage
            }
          )
          .then(function(res) {
            if (res.state == 0) {
              that.$message({
                message: "添加成功",
                type: "success"
              });
              that.$router.push("/member/list");
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },

    cancel(){
      this.$router.push("/member/list");
    }
  }
};
</script>


<style>
.boxing {
  padding: 0 50px;
  height: 500px;
  /* background: red; */
  margin: 0 auto;
}
h4 {
  text-align: center;
  line-height: 80px;
}
</style>



